import React, { useState, useContext } from 'react'
import { CaretUp, Palette, Pencil } from 'react-bootstrap-icons'
import Project from './Project'
import { ToDoContext } from './context/ToDoContextProvider'
import AddNewProject from './AddNewProject'
import { useSpring, animated } from 'react-spring'

function Projects() {
  const { selectedProject, setSelectedProject, projects } =
    useContext(ToDoContext)
  const [edit, setEdit] = useState(false)
  const [colour, setColor] = useState('')
  const [show, setShow] = useState(true)

  const projectAnimation = useSpring({
    transform: show ? 'rotate(0defg)' : 'rotate(180deg)',
    config: { friction: 10 },
  })

  const displayProject = useSpring({
    display: show ? 'block' : 'none',
    lineHeight: show ? 1.2 : 0,
  })

  return (
    <div className='projects'>
      <div className='title'>
        <animated.span style={projectAnimation} onClick={() => setShow(!show)}>
          <Palette />
        </animated.span>
        <span>Project</span>

        <div className='icon'>
          <span
            className='pencil'
            style={{ color: colour, cursor: 'pointer' }}
            onClick={() => {
              setEdit(!edit)
              colour === '' ? setColor('green') : setColor('')
            }}
          >
            <Pencil />
          </span>
          <AddNewProject />
          <animated.div
            className='caretUp'
            style={projectAnimation}
            onClick={() => setShow(!show)}
          >
            <CaretUp />
          </animated.div>
        </div>
      </div>

      <animated.div style={displayProject} className='special'>
        {projects.map((project) => (
          <Project
            project={project}
            edit={edit}
            selectedProject={selectedProject}
            setSelectedProject={setSelectedProject}
          />
        ))}
      </animated.div>
    </div>
  )
}

export default Projects

