import moment from 'moment'
import { useState, useEffect } from 'react'
import firebase from '../firebase'

export function useToDos() {
  const [todos, setTodos] = useState([])

  useEffect(() => {
    let unsubscribe = firebase
      .firestore()
      .collection('todos')
      .onSnapshot((snapshot) => {
        const data = snapshot.docs.map((doc) => {
          return {
            id: doc.id,
            ...doc.data(),
          }
        })
        // data is an array
        setTodos(data)
      })

    return () => {
      unsubscribe()
    }
  }, [])

  return todos
}

// we use useEffect because we set up subscription
export function useProjects() {
  const [projects, setProjects] = useState([])

  useEffect(() => {
    let unsubscribe = firebase
      .firestore()
      .collection('projects')
      .onSnapshot((snapshot) => {
        const data = snapshot.docs.map((doc) => {
          const projectName = doc.data().projectName
          return {
            id: doc.id,
            projectName: projectName,
          }
        })
        setProjects(data)
      })

    return () => {
      unsubscribe()
    }
  }, [])
  return projects
}

// docs.data return documents object

export function useFilterProject(selectedProject, todos) {
  const [filterProject, setFilterProject] = useState([])

  useEffect(() => {
    const todayDateFormated = moment().format('MM/DD/YYYY')
    let data
    if (selectedProject == 'all days') {
      data = todos
    } else if (selectedProject === 'today') {
      data = todos.filter((todo) => todo.date === todayDateFormated)
    } else if (selectedProject === 'next 7 days') {
      data = todos.filter((todo) => {
        const todoDate = moment(todo.date, 'MM/DD/YYYY')
        const todayDate = moment(todayDateFormated, 'MM/DD/YYYY')
        const diffDays = todoDate.diff(todayDate, 'days')
        return diffDays >= 0 && diffDays < 7
      })
    } else {
      data = todos.filter((todo) => todo.projectName === selectedProject)
    }
    setFilterProject(data)
  }, [todos, selectedProject])
  return filterProject
}

export function useProjectsWithStats(projects, todos) {
  const [projectsWithStats, setProjectsWithStats] = useState([])

  useEffect(() => {
    const data = projects.map((project) => {
      return {
        numOfTodos: todos.filter(
          (todo) => todo.projectName === project.projectName && !todo.checked
        ).length,
        ...project,
      }
    })

    setProjectsWithStats(data)
  }, [projects, todos])

  return projectsWithStats
}

// so when we get our todos from out firestore database. We pass them as an argument to my useProject to calculate numOfToDos and something wrong there. What's wrong about this is when we call the function useProjects. We make a call to our file store database and then we get back to our projects. When we call useToDos we make a call and then get our todos
// => we have 2 cases: if we get todos first => get our projects => calculate is fine
// but on the other hand if we get the projects first and then we didn't get yet the todos because this is a synchronous code => we don't get calculate and then when we get our todos from our database we don't go and rerun our useProject hook so we don't recalculate the numberOfToDo so we don't see any changes or any updates

