import React, { useContext, useEffect, useState } from 'react'
import ToDo from './ToDo'
import Next7Days from './Next7Days'
import { ToDoContext } from './context/ToDoContextProvider'
import moment from 'moment'

function Todos() {
  // const { selectedProject, setSelectedProject } = useContext(ToDoContext)
  //  by useContext we can get the value of Provider of ToDoContext instead of using Consumer to get value that we need to use

  const { selectedProject, todos } = useContext(ToDoContext)
  console.log(todos)
  const [weekDays, setWeekdays] = useState([])
  useEffect(() => {
    const arrange = ['0', '1', '2', '3', '4', '5', '6']
    const arrayTong = arrange.map((index) => {
      return {
        number: index,
        todos: todos.filter((todo) => todo.day === index),
      }
    })
    const days = parseInt(moment().format('d'))
    const arrangedTodos = arrayTong.slice(days).concat(arrayTong.slice(0, days))

    setWeekdays(arrangedTodos)
  }, [todos])

  console.log(weekDays)
  return (
    <div className='Todos'>
      <div className='selectedProject'> {selectedProject}</div>
      <div className='todos'>
        {selectedProject === 'next 7 days' ? (
          <Next7Days weekDays={weekDays} />
        ) : (
          todos.map((todo) => <ToDo todo={todo} />)
        )}
      </div>
    </div>
  )
}

export default Todos

