import React, { useContext, useState } from 'react'
import { useSpring, animated } from 'react-spring'

import calendars from './const'
import { Palette, CaretUp } from 'react-bootstrap-icons'
import { ToDoContext } from './context/ToDoContextProvider'
function Calendar() {
  const { selectedProject, setSelectedProject } = useContext(ToDoContext)
  const [show, setShow] = useState(true)

  const calendarAnimation = useSpring({
    transform: show ? 'rotate(0defg)' : 'rotate(180deg)',
    config: { friction: 10 },
  })

  const displayCalendar = useSpring({
    display: show ? 'block' : 'none',
    lineHeight: show ? 1.2 : 0,
  })
  return (
    <div className='calendar-content'>
      <div className='title'>
        <div>
          <p>
            <Palette />
            <span style={{ paddingLeft: '10px' }}>Calendar</span>
          </p>
        </div>

        <animated.div
          style={calendarAnimation}
          onClick={() => setShow(!show)}
          className='caretUp'
        >
          <CaretUp />
        </animated.div>
      </div>
      {calendars.map((calendar) => (
        <animated.div className='calendar' style={displayCalendar}>
          <p onClick={() => setSelectedProject(calendar)}>{calendar}</p>
        </animated.div>
      ))}
    </div>
  )
}

export default Calendar
