import React, { useState, useContext, useEffect } from 'react'
import TodoForm from './TodoForm'
import { ToDoContext } from './context/ToDoContextProvider'
import moment from 'moment'
import firebase from './firebase'

function EditToDo() {
  // CONTEXT
  const { projects, selectedToDo } = useContext(ToDoContext)
  const [nameProject, setNameProject] = useState('')

  // state
  const [time, setTime] = useState(new Date())
  const [day, setDay] = useState(new Date())
  const [text, setText] = useState('')

  useEffect(() => {
    if (selectedToDo) {
      setText(selectedToDo.text)
      setDay(moment(selectedToDo.date, 'MM/DD/YYYY'))
      setTime(moment(selectedToDo.time, 'hh:mm A'))
      setNameProject(selectedToDo.projectName)
    }
  }, [selectedToDo])

  useEffect(() => {
    if (selectedToDo) {
      firebase
        .firestore()
        .collection('todos')
        .doc(selectedToDo.id)
        .update({
          text,
          date: moment(day).format('MM/DD/YYYY'),
          day: moment(day).format('d'),
          time: moment(time).format('hh:mm A'),
          projectName: nameProject,
        })
    }
  }, [time, day, text, nameProject])
  function handleSubmit(e) {
    e.preventDefault()
  }

  return (
    <>
      {selectedToDo && (
        <div className='EditToDo'>
          <TodoForm
            handleSubmit={handleSubmit}
            text={text}
            setText={setText}
            day={day}
            time={time}
            setDay={setDay}
            setTime={setTime}
            projects={projects}
            nameProject={nameProject}
            setNameProject={setNameProject}
            confirmButton={true}
          />
        </div>
      )}
    </>
  )
}

export default EditToDo
