import React, { useState, useContext } from 'react'
import { CheckCircle, Circle, ArrowRepeat, Trash } from 'react-bootstrap-icons'
import firebase from './firebase'
import moment from 'moment'
import { ToDoContext } from './context/ToDoContextProvider'
import { useTransition, useSpring, animated } from 'react-spring'

function ToDo({ todo }) {
  const [mouseOver, setMouseOver] = useState(false)
  const { selectedToDo, setSelectedToDo } = useContext(ToDoContext)
  console.log(todo)
  const deleteToDo = (todo) => {
    firebase.firestore().collection('todos').doc(todo.id).delete()
  }

  const handleToDo = (todo) => {
    deleteToDo(todo)
    if (todo === selectedToDo) {
      setSelectedToDo(undefined)
    }
  }

  const check = todo.checked
  const checkToDo = (todo) => {
    firebase.firestore().collection('todos').doc(todo.id).update({
      checked: !todo.checked,
    })
  }
  // !true = false và !false = true

  const repeateToDo = (todo) => {
    const nextDayDate = moment(todo.date, 'MM/DD/YYYY').add(1, 'days')

    const repeatedTODO = {
      ...todo,
      checked: false,
      date: nextDayDate.format('MM/DD/YYYY'),
      day: nextDayDate.format('d'),
    }

    delete repeatedTODO.id

    firebase.firestore().collection('todos').add(repeatedTODO)
  }

  // ANIMATION
  const fadeIn = useSpring({
    from: { marginTop: '-12px', opacity: 0 },
    to: { marginTop: '0px', opacity: 1 },
  })

  const checkTransitions = useTransition(todo.checked, {
    from: { position: 'absolute', transform: 'scale(0)' },
    enter: { transform: 'scale(1)' },
    leave: { transform: 'scale(0)' },
  })

  return (
    <animated.div
      style={fadeIn}
      className='todo'
      onMouseOver={() => setMouseOver(true)}
      onMouseLeave={() => setMouseOver(false)}
    >
      <div className='check' onClick={() => checkToDo(todo)}>
        {checkTransitions((props, checked) =>
          checked ? (
            <animated.span style={props}>
              <CheckCircle />
            </animated.span>
          ) : (
            <animated.span style={props}>
              <Circle />
            </animated.span>
          )
        )}
      </div>
      <div className='content'>
        <div className='text'>
          <div className='checked'>
            {/* {todo.text}
            {check && <div className='slide'></div>} */}
            <p
              style={{ color: check ? '#bebebe' : '#000', cursor: 'pointer' }}
              onClick={() => setSelectedToDo(todo)}
            >
              {todo.text}
            </p>
            <div className={`line ${check ? 'line-through' : ''}`}></div>
          </div>
        </div>
        <div className='time-day'>
          <div className='time'>{todo.time} </div>
          <div className='day'>{todo.date}</div>
          <div className='project'>{todo.projectName}</div>
        </div>
      </div>
      <div className='icon'>
        <span>
          {check && <ArrowRepeat onClick={() => repeateToDo(todo)} />}
        </span>
        <span onClick={() => handleToDo(todo)}>
          {(check || mouseOver) && <Trash />}
        </span>
      </div>
    </animated.div>
  )
}

export default ToDo

// why have to use position absolute please read this
/*
As you suspect, the initial values for all these properties is not 0; it is auto. You can find their property definitions in section 9.3.2 of the spec.

When an absolutely positioned box keeps all its offsets auto (i.e. you don't modify them), it "doesn't go anywhere"(important you have to read). It remains in the static position, which basically means its usual spot in the layout had it not been positioned at all. Section 10 has all the details (it even has entire paragraphs explaining what "static position" means), but you'll want to focus on 10.3.7:

The constraint that determines the used values for these elements is:

'left' + 'margin-left' + 'border-left-width' + 'padding-left' + 'width' + 'padding-right' + 'border-right-width' + 'margin-right' + 'right' = width of containing block

If all three of 'left', 'width', and 'right' are 'auto': First set any 'auto' values for 'margin-left' and 'margin-right' to 0. Then, if the 'direction' property of the element establishing the static-position containing block is 'ltr' set 'left' to the static position and apply rule number three below; otherwise, set 'right' to the static position and apply rule number one below.

[...]

1. 'left' and 'width' are 'auto' and 'right' is not 'auto', then the width is shrink-to-fit. Then solve for 'left'

And 10.6.4:

For absolutely positioned elements, the used values of the vertical dimensions must satisfy this constraint:

'top' + 'margin-top' + 'border-top-width' + 'padding-top' + 'height' + 'padding-bottom' + 'border-bottom-width' + 'margin-bottom' + 'bottom' = height of containing block

If all three of 'top', 'height', and 'bottom' are auto, set 'top' to the static position and apply rule number three below.

[...]

3. 'height' and 'bottom' are 'auto' and 'top' is not 'auto', then the height is based on the content per 10.6.7, set 'auto' values for 'margin-top' and 'margin-bottom' to 0, and solve for 'bottom'
*/
