import React from 'react'
import img from '../images/logo.jpg'
function Login() {
  return (
    <div className='login'>
      <div className='image'>
        <img src={img} alt='logo' />
      </div>
      <div className='title'>
        <p>Code explained</p>
        <p>
          <a href='#'>Login</a>
        </p>
      </div>
    </div>
  )
}

export default Login
