import React, { useState } from 'react'
import RenameProject from './RenameProject'
import Modal from './Modal'
import { Pencil, Trash } from 'react-bootstrap-icons'
import firebase from './firebase'
import { useTransition, useSpring, animated } from 'react-spring'
function Project({ project, edit, setSelectedProject, selectedProject }) {
  console.log(project.numOfTodos)
  const [modal, setModal] = useState(false)
  console.log(project)
  const deleteProject = (project) => {
    firebase
      .firestore()
      .collection('projects')
      .doc(project.id)
      .delete()
      .then(() => {
        firebase
          .firestore()
          .collection('todos')
          .where('projectName', '==', project.projectName)
          .get()
          .then((querySnapshot) => {
            querySnapshot.forEach((todo) => {
              todo.ref.delete()
            })
          })
          .then(() => {
            if (selectedProject === project.projectName) {
              setSelectedProject('today')
            }
          })
      })
  }

  // ANIMATION
  const fadeIn = useSpring({
    from: { marginTop: '-12px', opacity: 0 },
    to: { marginTop: '0px', opacity: 1 },
  })

  const btnTransitions = useTransition(edit, {
    from: { opacity: 0, marginLeft: '-20px' },
    enter: { opacity: 1, marginLeft: '0px' },
    leave: { opacity: 0, marginLeft: '-20px' },
  })
  // then giúp ta kiểm soát được thứ tự các hành động thực hiện
  return (
    <animated.div className='project' style={fadeIn}>
      <div onClick={() => setSelectedProject(project.projectName)}>
        {project.projectName}
      </div>
      <div className='ICON'>
        {btnTransitions((props, editProject) =>
          editProject ? (
            <animated.div style={props} className='icon'>
              <span className='pencil' onClick={() => setModal(true)}>
                <Pencil />
              </span>

              <span onClick={() => deleteProject(project)}>
                <Trash />
              </span>
            </animated.div>
          ) : project.numOfTodos === 0 ? (
            ''
          ) : (
            <animated.div style={props} className='numOfToDo'>
              {project.numOfTodos}
            </animated.div>
          )
        )}
      </div>

      <Modal modal={modal} setModal={setModal}>
        <RenameProject modal={modal} setModal={setModal} project={project} />
      </Modal>
    </animated.div>
  )
}

export default Project
