import React, { useState, useContext } from 'react'
import firebase from './firebase'
import { ToDoContext } from './context/ToDoContextProvider'
function RenameProject({ modal, setModal, project }) {
  // const [value, setValue] = useState(project.projectName)
  const [newProject, setNewProject] = useState(project.projectName)
  console.log(newProject)

  // CONTEXT
  const { selectedProject, setSelectedProject } = useContext(ToDoContext)

  const renameProject = (project, newProject) => {
    const projectRef = firebase.firestore().collection('projects')
    const toDoRef = firebase.firestore().collection('todos')

    const old = project.projectName
    console.log(old)

    projectRef
      .where('projectName', '==', newProject)
      .get()
      .then((querySnapshot) => {
        console.log(querySnapshot)
        if (!querySnapshot.empty) {
          alert('the project is already exist!')
        } else {
          projectRef
            .doc(project.id)
            .update({
              projectName: newProject,
            })
            .then(() => {
              toDoRef
                .where('projectName', '==', old)
                .get()
                .then((querySnapshot) => {
                  querySnapshot.forEach((doc) => {
                    doc.ref.update({
                      projectName: newProject,
                    })
                  })
                })
            })
            .then(() => {
              if (selectedProject === old) {
                setSelectedProject(newProject)
              }
            })
        }
      })
  }
  const handleSubmit = (e) => {
    e.preventDefault()

    renameProject(project, newProject)
    setModal(false)
  }
  return (
    <div className='rename'>
      {modal && (
        <form onSubmit={handleSubmit}>
          <p className='title'> ReName Project </p>
          <input
            type='text'
            value={newProject}
            onChange={(e) => setNewProject(e.target.value)}
            placeholder='project name...'
            autoFocus
          />

          <div className='confirm'>
            <div className='add'>
              <button>add</button>
            </div>

            <div className='cancel'>
              <button onClick={() => setModal(false)}>cancel</button>
            </div>
          </div>
        </form>
      )}
    </div>
  )
}

export default RenameProject
