import { weeksToDays } from 'date-fns'
import React from 'react'
import ToDo from './ToDo'
function Next7Days({ weekDays }) {
  const date = [
    'Sunday',
    'Monday',
    'Tuesday',
    'Wedsday',
    'Thursday',
    'Friday',
    'Saturday',
  ]

  return (
    <div className='Next7Days'>
      {weekDays.map((todos) => {
        return (
          <div className='each day'>
            <p className='day'>{date[todos.number]}</p>
            {todos.todos.map((todo) => (
              <ToDo todo={todo} />
            ))}
          </div>
        )
      })}
    </div>
  )
}

export default Next7Days
