import React, { useState, useContext, useEffect } from 'react'
import Modal from './Modal'
// import projects from './projects/projects'
// import ProjectForm from './ProjectForm'

import TodoForm from './TodoForm'
import calendars from './const'
import firebase from './firebase'
import moment from 'moment'
import { ToDoContext } from './context/ToDoContextProvider'
function AddNewToDo() {
  const [modal, setModal] = useState(false)
  const [time, setTime] = useState(new Date())
  const [day, setDay] = useState(new Date())
  const [text, setText] = useState('')

  const { selectedProject, projects, todos } = useContext(ToDoContext)
  const [nameProject, setNameProject] = useState(selectedProject)

  useEffect(() => {
    setNameProject(selectedProject)
  }, [selectedProject])

  const handleSubmit = (e) => {
    e.preventDefault()

    if (text && !calendars.includes(nameProject)) {
      const textArray = todos.map((todo) => todo.text)
      const dayArray = todos.map((todo) => todo.date)
      const name = todos.map((todo) => todo.projectName)
      const timeArray = todos.map((todo) => todo.time)

      if (
        textArray.includes(text) &&
        name.includes(nameProject) &&
        dayArray.includes(moment(day).format('MM/DD/YYYY')) &&
        timeArray.includes(moment(time).format('hh:mm A'))
      ) {
        return alert('the data is already exist')
      } else {
        firebase
          .firestore()
          .collection('todos')
          .add({
            text: text,
            date: moment(day).format('MM/DD/YYYY'),
            day: moment(day).format('d'),
            time: moment(time).format('hh:mm A'),
            checked: false,
            projectName: nameProject,
          })

        setModal('false')
        setText('')
        setDay(new Date())
        setTime(new Date())
      }
    }
  }

  return (
    <div className='AddNewToDo'>
      <div
        className='btn'
        onClick={() => {
          setModal(true)
        }}
      >
        <button className='btn-addNewToDo'>add new to do</button>
      </div>
      <Modal modal={modal} setModal={setModal}>
        {/* <ProjectForm
          heading='Add new to do...'
          handleSubmit={handleSubmit}
          setModal={setModal}
          confirmButtonText='Add new to do'
        /> */}
        <TodoForm
          handleSubmit={handleSubmit}
          heading='Add new to do'
          text={text}
          setText={setText}
          day={day}
          time={time}
          setTime={setTime}
          setDay={setDay}
          projects={projects}
          cancel={true}
          confirmButton={true}
          setModal={setModal}
          nameProject={nameProject}
          setNameProject={setNameProject}
        />
      </Modal>
    </div>
  )
}

export default AddNewToDo

