import React from 'react'
import {
  TimePicker,
  DatePicker,
  MuiPickersUtilsProvider,
} from '@material-ui/pickers'
import DateFnsUtils from '@date-io/date-fns'
import { Bell, Clock, Calendar, X } from 'react-bootstrap-icons'
function TodoForm({
  handleSubmit,
  heading = false,
  text,
  setText,
  day,
  time,
  setTime,
  setDay,
  projects,
  cancel = false,
  confirmButton = false,
  setModal,
  nameProject,
  setNameProject,
}) {
  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <form className='TodoForm' onSubmit={handleSubmit}>
        <div className='Projects'>
          <h3>{heading}</h3>
          <input
            type='text'
            value={text}
            onChange={(e) => setText(e.target.value)}
            placeholder='To do ...'
            autoFocus
          />
          <div className='title'>
            <Bell /> <span> Remind Me ! </span>
          </div>
          <div className='showTime'>
            <span>
              <Clock /> <span> Choose time </span>
            </span>
            <TimePicker
              value={time}
              onChange={(time) => setTime(time)}
              className='time'
            />
          </div>

          <div className='showDay'>
            <span>
              <Calendar />
              <span>Choose a day</span>
            </span>
            <DatePicker
              value={day}
              onChange={(day) => setDay(day)}
              className='day'
            />
          </div>
          <div className='projects'>
            {projects.length > 0 ? (
              projects.map((project) => (
                <div
                  className={
                    project.projectName === nameProject
                      ? 'project active'
                      : 'project'
                  }
                  onClick={() => setNameProject(project.projectName)}
                >
                  {project.projectName}
                </div>
              ))
            ) : (
              <div style={{ color: 'red' }}>
                Please add a project before proceeding{' '}
              </div>
            )}
          </div>
        </div>

        {cancel && (
          <div className='exit' onClick={() => setModal(false)}>
            <X />
          </div>
        )}

        {confirmButton && (
          <button className='confirmBtn'>+ add new to do</button>
        )}
      </form>
    </MuiPickersUtilsProvider>
  )
}

export default TodoForm
