import React, { useSpring, animated } from 'react-spring'
import { useRef } from 'react'
function Modal({ setModal, modal, children }) {
  const modalUseRef = useRef()

  function Hide(e) {
    // console.log(e.target)
    // console.log(modalUseRef.current)
    return modalUseRef.current === e.target ? setModal(false) : ''
  }

  const modalAnimation = useSpring({
    opacity: modal ? 1 : 0,
    top: modal ? '45%' : '0%',
    config: { friction: 15 },
  })
  return (
    modal && (
      <div className='container' ref={modalUseRef} onClick={(e) => Hide(e)}>
        <animated.div className='modal' style={modalAnimation}>
          {children}
        </animated.div>
      </div>
    )
  )
}

export default Modal

